/* stylelint-disable font-family-no-missing-generic-family-keyword */
body {
  margin: 0;
  padding: 0 10px 0 8px;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-book.ttf);
}

@font-face {
  font-family: Heavy;
  src: url(./fonts/Avenir-Heavy.ttf);
}

.search-line-large {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  height: 68px;
  box-shadow: 0 1px 8px 0 rgba(61, 78, 97, 0.1);
  border: 1px solid #e1e7ed;
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 4px;
}

.search-line-large:hover {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.search-line-large:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;

}

input::placeholder {
  color: #3d4e61;
  font-family: Avenir;
  font-style: normal;
  line-height: normal;
}

.text-search-line-large {
  font-family: Avenir;
  color: #3d4e61;
  border: none;
  font-size: 16px;
  height: 19px;
  width: 100%;
  font-weight: 300;
  line-height: normal;
}

.text-search-line-large:focus {
  font-family: Heavy;
  outline: none;
  border: none;
}

.img-search-line-large {
  margin-left: 25px;
  margin-right: 17px;
  width: 19px;
  height: 19px;
  transform: translateY(-1px);
}

/* small-form */
.search-line-small {
  width: 100%;
  box-shadow: 0 1px 8px 0 rgba(61, 78, 97, 0.1);
  border: 1px solid #e1e7ed;
  background: #fff;
  display: flex;
  flex-direction: row;
  border-radius: 4px;
  height: 40px;
  align-items: center;
}

.search-line-small:hover {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.search-line-small:focus {
  border-radius: 3px;
  outline: none;
  border: none;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

.text-search-line-small {
  font-family: Avenir;
  color: #3d4e61;
  font-size: 14px;
  width: 100%;
  font-weight: 300;
  font-style: normal;
  line-height: normal;
  outline: none;
  border: none;
}

.text-search-line-small:focus {
  font-family: Heavy;
}

.img-search-line-small {
  margin-left: 12px;
  margin-right: 9px;
  width: 11px;
  height: 11px;
  transform: translateY(-1px);
}
